/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {

    .edgtf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: $dark-box-background-color;
            text-align: center;
            box-sizing: border-box;
            border: none;
        }

        .edgtf-quantity-input {
            position: relative;
            height: 54px;
            width: 54px;
            padding: 0;
            margin: 0 29px 0 0;
            font-family: inherit;
            line-height: 52px;
            outline: 0;
            font-weight: 700;
            font-size: 17px;
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            position: absolute;
            right: 0;
            width: 25px;
            height: 25px;
            line-height: 25px;
            border-left: 0;
            cursor: pointer;
            @include edgtfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .edgtf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */