/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();
	
	&.edgtf-info-on-image {
		
		.edgtf-pli {
			
			.edgtf-pli-add-to-cart {
				margin: 25px 0 0;
			}

			.edgtf-pli-title {
				float: none;
			}
			.edgtf-pli-price {
				text-align: center;
			}
		}
	}

    .edgtf-pli {
        padding: 0;
        z-index: 100;

	    .edgtf-pli-title {
			margin: 0;
			display: inline-block;
			float: left;
			width: 50%;
	    }
	
	    .edgtf-pli-category {
			display: block;
			font-size: 13px;
			font-weight: 300;
			float: left;
			width: 100%;
			margin-top: 1px;
			text-transform: lowercase;
	    }

        .edgtf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .edgtf-pli-rating-holder {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .edgtf-pli-rating {
		    @include edgtfWooCommerceRatingsStyle();
	    }

        .edgtf-pli-price {
	        @include edgtfWooCommercePriceStyle();
			display: inline-block;
			text-align: right;
			width: 50%;
			margin: 0;
        }
    }

    .edgtf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .edgtf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
	            @include edgtfWooCommerceMarksGeneralStyle();
            }

            .edgtf-pli-onsale {
	            @include edgtfWooCommerceSaleStyle();
            }

            .edgtf-pli-out-of-stock {
	            @include edgtfWooCommerceOutOfStockStyle();
            }
	
	        .edgtf-pli-new-product {
		        @include edgtfWooCommerceNewProductStyle();
	        }
        }
            
        .edgtf-pli-text {
            @include edgtfAbsoluteHolderLayout();
            padding: 0;
            opacity: 0;
            box-sizing: border-box;
            @include edgtfTransition(opacity .2s ease-out);
        }

        .edgtf-pli-text-outer {
	        @include edgtfTableLayout();
	        text-align: center;
        }

        .edgtf-pli-text-inner {
            @include edgtfTableCellLayout();
			vertical-align: bottom;

            .edgtf-pli-title,
            .edgtf-pli-category a,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating,
            .edgtf-pli-price {
                color: #fff;
            }
	
	        .edgtf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .edgtf-pli-add-to-cart {

		        &.edgtf-default-skin {

			        .button,
			        .added_to_cart {
				        @include edgtfButtonSolidColor();
						@include edgtfTransition(.5s cubic-bezier(0.22, 0.61, 0.36, 1));

				        &:hover {
							-webkit-filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
							filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
				        }
			        }
		        }

		        &.edgtf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $first-main-color;
				        background-color: #fff;

				        &:hover {
							-webkit-filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
							filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
				        }
			        }
		        }
		
		        &.edgtf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: #000;
				
				        &:hover {
							-webkit-filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
							filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include edgtfButtonDefaultStyle();
	            @include edgtfButtonSize();
	            white-space: nowrap;
	            z-index: 3;

				&:after {
					content: '';
					position: absolute;
					top: -1px;
					right: -14px;
					width: 0;
					height: 0;
					border-bottom: 54px solid $first-main-color;
					border-right: 13px solid transparent;
					@include edgtfTransition(all .2s ease-in-out);
				}

				&:before {
					content: '';
					position: absolute;
					top: -1px;
					left: -14px;
					width: 0;
					height: 0;
					border-bottom: 54px solid $first-main-color;
					border-left: 13px solid transparent;
					@include edgtfTransition(all .2s ease-in-out);
				}
            }

            .button.added {
                display: none;
            }
        }

	    .edgtf-pli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }
    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();
        margin: 25px 0 0;
    }
	
	&.edgtf-product-info-light {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #fff;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.edgtf-product-info-dark {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $default-heading-color;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.edgtf-masonry-layout {
		
		.edgtf-pli-inner {
			width: 100%;
			height: 100%;
			
			.edgtf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */