/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

/* block widgets start */
.widget {

    &.widget_block {

        /* default styling for all widgets */
        .wc-block-product-categories {
            margin-bottom: 0;

            ul {
                padding: 0;
                margin: 0;
                list-style: none;

                li {
                    @include edgtfRelativeHolderLayout();
                    padding: 0;
                    margin: 0 0 14px;
                    clear: both;

                    &:last-child {
                        margin: 0;
                    }

                    ul {
                        margin: 14px 0 0 10px;
                    }
                }
            }
        }

        /* specific style */
        .wc-block-product-categories {

            ul {

                li {
                    margin: 2px 0;
                    padding: 12px 21px 15px;
                    border-bottom: 1px solid $default-border-color;
                    box-sizing: border-box;

                    &:last-child {
                        padding-bottom: 15px;
                    }

                    a {
                        font-size: 17px;
                        @include edgtfTransform(color .2s ease-out);

                        &:hover {
                            @include edgtfTransform(color .2s ease-out);
                        }
                    }

                    ul {
                        margin: 0 0 0 10px;
                    }
                }

                .wc-block-product-categories-list-item-count {
                    display: none;
                }
            }
        }


        .wc-block-product-search {

            .wc-block-product-search__label {
                display: none;
            }

            .wc-block-product-search__field {
                @include edgtfPlaceholder {
                    color: #c0c0c0;
                }
            }

            input,
            button {
                position: relative !important;
                display: inline-block !important;
                vertical-align: top !important;
                float: left !important;
                margin: 0 !important;
                height: 54px !important;
                line-height: 32px !important;
                color: $default-text-color !important;
                background-color: transparent !important;
                border: 0 !important;
                border-radius: 0 !important;
                outline: 0 !important;
                -webkit-appearance: none !important;
                box-sizing: border-box !important;
                @include edgtfTransition(color .2s ease-out);
            }

            input {
                width: calc(100% - 40px) !important;
                padding: 10px 10px 10px 20px !important;
                font-family: inherit !important;
                font-size: 17px !important;
                line-height: 26px !important;

                @include edgtfPlaceholder {
                    color: inherit;
                }
            }

            button {
                width: 40px !important;
                padding: 10px 13px 10px 4px !important;
                font-size: 18px !important;
                cursor: pointer !important;
                color: $default-heading-color !important;

                svg {
                    display: none;
                }

                &:after {
                    content: "\f002";
                    font-family: 'Font Awesome\ 5 Free';
                    font-weight: 900;
                    -moz-osx-font-smoothing: grayscale;
                    -webkit-font-smoothing: antialiased;
                    display: inline-block;
                    font-style: normal;
                    font-variant: normal;
                    text-rendering: auto;
                    line-height: 1;
                }

                &:hover {
                    filter: none !important;
                    -webkit-filter: none !important;
                    color: $default-text-color !important;
                }
            }
        }
    }
}
/* block widgets end */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include edgtfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include edgtfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include edgtfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .edgtf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .edgtf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            padding: 40px 20px 24px;
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: rgba(255,255,255,.3);
                
                .ui-slider-handle {
                    position: absolute;
                    top: 0;
                    height: 0;
                    width: 0;
                    background-color: $first-main-color;
                    margin: 0;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;

                    &:after {
                        content: '';
                        display: inline-block;
                        width: 0;
                        height: 0;
                        border-right: 5px solid transparent;
                        border-left: 5px solid transparent;
                        border-bottom: 8px solid red;
                        position: relative;
                        top: -5px;
                        left: -2px;
                        vertical-align: top;
                    }

                    &:last-of-type {
                        &:after{
                            left: -8px;
                        }
                    }
                    
                    &:last-child {
                        @include edgtfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        div.price_slider_amount {
            margin: 20px 0 0;
            
            button.button {
                float: left;
                background-color: transparent;
                padding: 0;

                &:hover {
                    background-color: transparent;
                    -webkit-filter: drop-shadow(3px 3px 20px rgba(#fff,.7));
                    filter: drop-shadow(3px 3px 20px rgba(#fff,.7));
                }
            }
            
            .price_label {
                float: right;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 2px 0;
                padding: 12px 21px 15px;
                border-bottom: 1px solid $default-border-color;
                box-sizing: border-box;

                &:last-child {
                    padding-bottom: 15px;
                }

                a {
                    font-size: 17px;
                    @include edgtfTransform(color .2s ease-out);

                    &:hover {
                        @include edgtfTransform(color .2s ease-out);
                    }
                }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .edgtf-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 115px;
                padding: 20px;
                border-bottom: 1px solid $default-border-color;
                box-sizing: border-box;
                margin: 0;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 20px;
                        left: 20px;
                        width: 100%;
                        max-width: 75px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 16px 0 0 95px;
                        box-sizing: border-box;
                        font-size: 17px;
                        line-height: 1.23em;
                        letter-spacing: 0.02em;
                        text-transform: uppercase;
                        font-weight: 700;
                        color: #fff;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 95px;
                    line-height: 20px;
                    font-size: 13px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
                
                .amount {
                    color: $default-text-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;
                
                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include edgtfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include edgtfRelativeHolderLayout();
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include edgtfTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: inherit;
                font-size: 17px;
                line-height: 26px;
    
                @include edgtfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 18px;
                cursor: pointer;
                color: $default-heading-color;
                
                &:hover {
                    color: $default-text-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */
