/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {
	
	&.edgtf-woo-single-thumb-on-left-side {
		
		.edgtf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 126px;
							width: calc(100% - 126px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 106px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 52%;
		    margin: 0;
		    padding: 0 24px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include edgtfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 48%;
        padding: 63px 0 0 46px;
        float: left;
        box-sizing: border-box;
	
	    .edgtf-single-product-title {
		    display: block;
		    margin: 0 0 7px;
	    }
	
	    .price {
		    @include edgtfWooCommercePriceStyle();
			font-size: 35px;

	    }
	
	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 18px 0 3px;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
		    margin: 20px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 32px 0 0;

		    > span {
			    display: block;
			    margin: 10px 0;
				color: #fff;
				font-size: 17px;
				font-weight: 700;
				text-transform: uppercase;

				span,
				a {
					color: $default-text-color;
					font-weight: 300;
                    text-transform: none;

					&:first-of-type{
						padding-left: 22px;
					}
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
		    margin: 0;

			.edgtf-social-share-holder.edgtf-list {
				.edgtf-social-title{
					color: #fff;
					font-size: 17px;
					font-weight: 700;
					text-transform: uppercase;
				}
				li a {
					font-size: 13px;
				}
			}
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 32px 0 0;
	    }
	
	    .edgtf-quantity-buttons {
		    margin: 0 35px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include edgtfRelativeHolderLayout();
		margin: 72px 0 0;
		background-color: $dark-box-background-color;
		
		ul.tabs {
			@include edgtfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: left;
			clear: both;
			overflow: hidden;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				@include edgtfTransition(background-color 1s cubic-bezier(.645,.045,.355,1));
				@include edgtfTransform(skew(20deg));
				transform-origin: 0 100%;
				float: left;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 11px 35px 11px 40px;
					font-size: 13px;
					font-weight: 500;
					text-transform: uppercase;
					color: #fff;
					@include edgtfTransform(skew(-20deg));
					@include edgtfTransition(color .2s ease-out,background-color .2s ease-out,border-color .2s ease-out);
				}

				&.active,
				&:hover {
					background-color: $first-main-color;
					@include edgtfTransition(all .2s ease-in-out);

					&:after {
						border-bottom-color: $first-main-color;
						@include edgtfTransition(all .2s ease-in-out);
					}
				}
			}
		}
		
		.entry-content {
			@include edgtfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 37px 40px 46px;
			}

            &.woocommerce-Tabs-panel--description {
                padding: 40px 38px 54px;
                box-sizing: border-box;
            }
		}
		
		table {
			margin: 37px 0 46px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				text-transform: uppercase;
				padding-left: 40px;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include edgtfRelativeHolderLayout();
			padding: 37px 40px 46px;
			box-sizing: border-box;
			
			h2 {
                font-size: 22px;
                line-height: 30px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include edgtfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;

                        .woocommerce-review__author {
                            font-size: 17px;
                            line-height: 21px;
                            letter-spacing: 0.02em;
                            color: $default-heading-color;
                            font-weight: 700;
                            text-transform: uppercase;
                        }
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 100px 0 0;
		
		> h2 {
			display: none;
		}
		
		.product {
			
			.edgtf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */