/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edgtf-woo-search-widget-button):not(.edgtf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgtfButtonDefaultStyle();
        @include edgtfButtonSize(large);
        @include edgtfButtonSolidColor();
        @include edgtfTransition(.5s cubic-bezier(0.22, 0.61, 0.36, 1));
        cursor: pointer;
        z-index: 3;
        /*padding: 9px 44px;*/

        &:hover {
            -webkit-filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
            filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */