/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 30px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: 50px;
        font-size: 17px;
    }

    .woocommerce-ordering {
        text-align: right;

        .select2-selection--single {
            background-color: $dark-box-background-color;
            border-color: $dark-box-background-color;
        }
        
        .orderby {
            width: auto;
	        min-width: 250px;
	        margin: 0;
	        text-align: initial;
        }
    }
}
/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */