/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .edgtf-cart-number {
            display: none;
        }

        &.edgtf-header-cart-icon-pack {
            
            .edgtf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: -42px;
    width: 266px;
    background-color: rgba(15,15,15,.9);
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include edgtfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 0;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 20px;
            border-bottom: 1px solid $default-border-color;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                border-bottom: 0;
                font-size: 13px;
                letter-spacing: .06em;
                font-weight: 400;
                text-transform: uppercase;
                line-height: 24px;
                color: #efefef;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 60px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .edgtf-product-title {
            display: block;
            margin: 8px 0 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: 8px;
            right: -5px;
	        font-size: 18px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            display: inline-block;
            margin: 2px 0 0;
            font-size: 13px;

            &:after {
                content: 'x';
                margin-left: 3px;
            }
        }

        .amount {
            display: inline-block;
            margin: 2px 0 0;
            font-size: 13px;
        }
    }

    .edgtf-cart-bottom {

        padding: 0;

        .edgtf-subtotal-holder {
            position: relative;
            width: 100%;
            padding: 9px 20px 13px;
            margin: 0;
            border-bottom: 1px solid $default-border-color;
            box-sizing: border-box;

            > * {
                display: inline-block;
                vertical-align: middle;
                color: rgba($default-heading-color,.9);
                font-size: 17px;
                letter-spacing: 0.06em;
            }
        }

        .edgtf-btn-holder {
            padding: 12px 20px;
            box-sizing: border-box;

            > a {

                font-weight: 400;

                &:nth-child(2) {
                    float: right;
                }
            }
        }

        .edgtf-view-cart {
            @include edgtfRelativeHolderLayout();
            height: 44px;
            line-height: 44px;
            padding: 0 26px;
            font-size: 10px;
            font-weight: inherit;
	        letter-spacing: 2px;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */